// SECTION scheduler :: START

.scheduler {
  &__title {
    padding-bottom: 40px;
    font-weight: 900;
    font-size: 40px;
    line-height: 50px;
    color: #000;
    @include adaptive(1279) {
      font-size: 34px;
      line-height: 40px;
    }
    @include adaptive(767) {
      padding-bottom: 30px;
      font-size: 24px;
      line-height: normal;
    }

    &--white {
      color: #ffffff;
    }
  }

  &__header {
    overflow: hidden;
    position: relative;
    background-color: #ffffff;

    .c-grid {
      position: relative;
    }

    &-wrapper {
      display: flex;
      align-items: center;
      height: 158px;
      @include adaptive(1279) {
        height: 108px;
      }
      @include adaptive(767) {
        height: 70px;
      }

      &-left {
        display: flex;
        align-items: center;

        span {
          display: block;
          width: 2px;
          height: 38px;
          margin-left: 20px;
          margin-right: 20px;
          background-color: rgba(#00449D, 0.1);
          @include adaptive(767) {
            margin-left: 15px;
            margin-right: 15px;
          }
        }

        p {
          font-weight: 300;
          font-size: 20px;
          color: #000;
          @include adaptive(767) {
            font-size: 18px;
          }
          @include adaptive(424) {
            font-size: 16px;
          }
          @include adaptive(374) {
            font-size: 14px;
          }
        }
      }

      &-right {
        @include adaptive(767) {
          display: none;
        }
      }
    }

    &-marker {
      position: absolute;
      top: -6px;
      right: 15px;
      @include adaptive(1279) {
        top: -36px;
      }
    }

    svg {
      fill: #00449D;
    }
  }

  &__main {
    padding-top: 110px;
    padding-bottom: 120px;
    background-color: #f8fafc;
    @include adaptive(767) {
      padding-top: 55px;
      padding-bottom: 60px;
    }

    &-subtitle {
      padding-bottom: 24px;
      font-weight: 300;
      font-size: 26px;
      color: #000;
      @include adaptive(1279) {
        font-size: 22px;
      }
      @include adaptive(767) {
        padding-bottom: 12px;
        font-size: 16px;
      }
    }

    &-title {
      padding-bottom: 37px;
      font-weight: normal;
      font-size: 50px;
      line-height: 70px;
      color: #000;
      @include adaptive(1279) {
        font-size: 40px;
        line-height: normal;

        br {
          display: none;
        }
      }
      @include adaptive(1023) {
        font-size: 33px;
      }
      @include adaptive(767) {
        font-size: 26px;
      }
    }

    &-heading {
      padding-bottom: 40px;
      font-weight: 900;
      font-size: 60px;
      line-height: 70px;
      color: #000;
      @include adaptive(1439) {
        font-size: 50px;
        line-height: 60px;
      }
      @include adaptive(1279) {
        font-size: 45px;
        line-height: 55px;
      }
      @include adaptive(767) {
        font-size: 26px;
        line-height: normal;
      }
    }

    &-filter {
      &-wrapper {
        display: none;
        flex-direction: column;
        padding-bottom: 10px;
        @include adaptive(767) {
          display: flex;
        }
      }

      &-head {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 45px;
        color: #00449d;

        &.is-open {
          svg {
            &.svg {
              &-arrow {
                opacity: 0;
                visibility: hidden;
                transform: scale(0) rotate(90deg);
                @include transition(opacity, visibility, transform);
              }
              &-close {
                opacity: 1;
                visibility: visible;
                transform: scale(1);
                @include transition(opacity, visibility, transform);
              }
            }
          }
        }

        p {
          font-weight: 600;
          font-size: 16px;
          color: #00449d;
        }

        svg {
          position: absolute;
          top: 50%;
          right: 21px;
          margin-top: -4px;
          width: 12px;
          height: 12px;

          &.svg {
            &-arrow {
              opacity: 1;
              visibility: visible;
              transform: rotate(90deg);
              @include transition(opacity, visibility, transform);
            }
            &-close {
              opacity: 0;
              visibility: hidden;
              transform: scale(0);
              @include transition(opacity, visibility, transform);
            }
          }
        }
      }

      &-body {
        display: none;

        .c-form {
          &__field {
            margin-bottom: 20px;
          }

          &__label {
            margin-bottom: 0;
            font-weight: 600;
            font-size: 14px;
            color: rgba(#000, 0.5);
          }

          &__select {
            border: 2px solid rgba(0, 0, 0, 0.05);
          }
        }
      }
    }

    &-dropdown {
      position: relative;
      display: flex;
      align-items: center;

      &-wrapper {
        display: flex;
        align-items: center;
        padding-bottom: 50px;
        @include adaptive(767) {
          display: none;
        }

        > div {
          padding-right: 60px;
          @include adaptive(1279) {
            padding-right: 30px;
            width: 100%;
            max-width: calc(100% / 3);

          }

          &:last-of-type {
            padding-right: 0;
          }
        }
      }

      &-title {
        padding-right: 16px;
        font-size: 20px;
        color: #222;
        @include adaptive(1279) {
          font-size: 18px;
        }
      }

      &-btn {
        cursor: pointer;
        padding-right: 7px;
        font-size: 20px;
        color: #00449D;
        @include adaptive(1279) {
          flex: 1;
          font-size: 18px;
          text-align: right;
        }

        svg {
          width: 10px;
          height: 10px;
          transform: rotate(90deg);
          fill: #00449D;
        }
      }

      &-menu {
        z-index: 899;
        display: none;
        position: absolute;
        top: calc(100% + 5px);
        right: 0;
        padding-top: 2px;
        padding-bottom: 3px;
        border-radius: 10px;
        background-color: #fff;
        box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.05);
      }

      &-link {
        display: flex;
        justify-content: flex-end;
        padding: 5px 15px 5px 30px;
        color: #00449D;
        font-size: 16px;
        line-height: 35px;
        @include transition(background-color);

        &:hover {
          background-color: lighten(#00449D, 67%);
          @include transition(background-color);
        }
      }
    }

    &-box {
      cursor: pointer;
      height: 100%;
      padding: 30px 15px 30px 30px;
      border-radius: 10px;
      background: #fff;
      box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.05);
      @include transition(box-shadow, background-color);
      @include adaptive(1023) {
        padding: 20px 10px 20px 20px;
      }
      @include adaptive(767) {
        white-space: normal;
      }

      &.is-active {
        box-shadow: none;
        background-color: #ecf1f6;
        @include transition(box-shadow, background-color);

        .scheduler__main-box-title,
        .scheduler__main-box-subtitle {
          opacity: 0.4;
          @include transition(opacity);
        }
      }

      &:hover {
        background-color: #00449d;
        @include transition(background-color);

        .scheduler__main-box-title,
        .scheduler__main-box-subtitle,
        .scheduler__main-box-desc {
          opacity: 1;
          color: #ffffff;
          @include transition(opacity, color);
        }
      }

      &-wrapper {
        display: flex;
        flex-wrap: wrap;
        width: calc(100% + 40px);
        margin-left: -40px;
        @include adaptive(1023) {
          width: calc(100% + 15px);
          margin-left: -15px;
        }
        @include adaptive(767) {
          overflow-x: auto;
          white-space: nowrap;
          position: relative;
          display: block;
          width: calc(100% + 30px);
          padding-bottom: 15px;
          font-size: 0;
        }

        > div {
          width: 100%;
          max-width: calc(100% / 3);
          padding-left: 40px;
          padding-bottom: 40px;
          @include adaptive(1023) {
            padding-left: 15px;
            padding-bottom: 15px;
          }
          @include adaptive(767) {
            display: inline-block;
            vertical-align: top;
            max-width: 295px;
          }
        }
      }

      p {
        @include transition(color);
      }

      &-title {
        padding-bottom: 5px;
        font-weight: 900;
        font-size: 22px;
        color: #000;
        @include transition(opacity, color);
      }

      &-subtitle {
        padding-bottom: 15px;
        font-size: 18px;
        color: #222;
        @include transition(opacity, color);
        @include adaptive(1023) {
          font-size: 16px;
        }
      }

      &-desc {
        overflow: hidden;
        position: relative;
        height: 44px;
        font-size: 16px;
        line-height: 22px;
        color: rgba(#222, 0.4);
        @include transition(opacity, color);

        //textarea {
        //  z-index: -1;
        //  opacity: 0;
        //  visibility: hidden;
        //  resize: none;
        //  position: absolute;
        //  top: 0;
        //  left: 0;
        //  width: 100%;
        //  height: auto;
        //  padding: 0;
        //  margin: 0;
        //  border: none;
        //  border-radius: 0;
        //  box-shadow: none;
        //  background-color: transparent;
        //  line-height: 22px;
        //}
      }
    }

    &-btn {
      display: none;
      @include adaptive(767) {
        padding-top: 15px;
      }
      
      .c-btn {
        max-width: unset;
        width: auto;
        padding-left: 60px;
        padding-right: 60px;
        @include adaptive(767) {
          position: relative;
          align-items: flex-start;
          justify-content: center;
          flex-direction: column;
          width: 100%;
          max-width: 425px;
          padding-left: 20px;
          padding-right: 52px;
          font-size: 16px;
        }

        &.is-active {
          svg {
            transform: rotate(-90deg);
            @include transition(transform);
          }
        }

        p {
          padding-right: 5px;
        }

        span {
          padding-right: 20px;
        }
      }

      svg {
        position: relative;
        top: 2px;
        width: 12px;
        height: 12px;
        transform: rotate(90deg);
        backface-visibility: hidden;
        transform-origin: center;
        @include transition(transform);
        @include adaptive(767) {
          position: absolute;
          top: 50%;
          margin-top: -10px;
          right: 20px;
        }
      }
    }
  }

  &__data {
    display: none;
    padding-top: 129px;
    padding-bottom: 60px;
    @include adaptive(767) {
      padding-top: 65px;
      padding-bottom: 30px;
    }

    &-list {
      display: flex;
      flex-direction: column;
      padding-bottom: 115px;
      @include adaptive(767) {
        flex-wrap: wrap;
        flex-direction: row;
        padding-bottom: 70px;
      }

      li {
        display: flex;
        align-items: center;
        padding-bottom: 20px;
        @include adaptive(767) {
          flex-wrap: wrap;
          width: 100%;
          max-width: 50%;
        }

        &:last-of-type {
          padding-bottom: 0;
          @include adaptive(767) {
            max-width: 100%;
          }
        }

        > div {
          &:nth-of-type(1) {
            width: 40px;
            padding-top: 3px;
            @include adaptive(767) {
              padding-top: 5px;
            }
          }
          &:nth-of-type(2) {
            width: 100%;
            max-width: 180px;
            @include adaptive(767) {
              max-width: calc(100% - 40px);
            }
          }
          &:nth-of-type(3) {
            flex: 1;
            min-width: 1px;
            max-width: 100%;
            @include adaptive(767) {
              padding-left: 40px;
            }
          }
        }

        p,
        span {
          font-size: 20px;
          color: #000;
          @include adaptive(767) {
            font-size: 16px;
          }
        }

        p {
          font-weight: 700;
        }
      }
    }

    &-option {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding-bottom: 35px;

      > div {
        &:nth-of-type(1),
        &:nth-of-type(3) {
          width: 100%;
          max-width: 180px;
          @include adaptive(767) {
            width: auto;
            max-width: unset;
          }
        }

        &:nth-of-type(2) {
          padding-left: 15px;
          padding-right: 15px;
          @include adaptive(767) {
            padding-left: 10px;
            padding-right: 10px;
          }
        }

        &:nth-of-type(3) {
          display: flex;
          justify-content: flex-end;
        }
      }

      h4 {
        font-size: 26px;
        font-weight: normal;
        text-align: center;
        color: #000;
        @include adaptive(1279) {
          font-size: 22px;
        }
        @include adaptive(767) {
          font-size: 18px;
        }
      }

      a {
        position: relative;
        display: flex;
        align-items: center;
        @include transition(color);

        &.swiper-button-disabled {
          pointer-events: none;
          color: rgba(#000, 0.4);
          @include transition(color);

          p,
          span {
            color: rgba(#000, 0.4);
            @include transition(color);
          }
        }
      }

      svg {
        width: 21px;
        height: 21px;
      }

      p,
      span {
        font-weight: bold;
        font-size: 20px;
        color: #00449d;
        @include transition(color);
        @include adaptive(1279) {
          font-size: 17px;
        }
        @include adaptive(767) {
          font-size: 14px;
          font-weight: 600;
        }
      }

      p {
        display: none;
        @include adaptive(767) {
          display: block;
        }
      }

      span {
        @include adaptive(767) {
          display: none;
        }
      }
    }

    &-prev {
      left: -15px;
      @include adaptive(1279) {
        left: 0;
      }

      svg {
        margin-right: 20px;
        @include adaptive(767) {
          margin-right: 10px;
        }
      }
    }

    &-next {
      right: -15px;
      @include adaptive(1279) {
        right: 0;
      }

      svg {
        margin-left: 20px;
        transform: scale(-1);
        @include adaptive(767) {
          margin-left: 10px;
        }
      }
    }

    &-slider {
      overflow: hidden;
      position: relative;
      left: -20px;
      width: calc(100% + 40px);
      padding-left: 20px;
      padding-right: 20px;
      padding-bottom: 30px;
      @include adaptive(767) {
        left: -15px;
        width: calc(100% + 30px);
        padding-left: 15px;
        padding-right: 15px;
      }
    }

    &-slide {
      position: relative;
      display: flex;
      align-items: center;
      flex-direction: column;
      width: 100%;
      height: 145px;
      padding: 10px;
      border-radius: 10px;
      background: #fff;
      box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.05);
      @include transition(box-shadow, background-color);
      @include adaptive(767) {
        height: 73px;
        padding: 5px;
      }

      &.is-active,
      &:hover {
        box-shadow: none;
        background-color: #00449d;
        @include transition(box-shadow, background-color);

        span,
        h5,
        p,
        h4 {
          color: #ffffff;
          @include transition(color);
        }
      }

      &.scheduler__data-slide--disabled {
        pointer-events: none;
        box-shadow: none;
        background-color: #f8f8f8;
        @include transition(box-shadow, background-color);

        span,
        h5,
        p,
        h4 {
          color: rgba(#000, 0.4);
          @include transition(color);
        }
      }

      &.scheduler__data-slide--today {
        @include adaptive(767) {
          h5,
          h4 {
            color: #cd084b;
          }
        }

        i,
        span {
          opacity: 1;
          visibility: visible;
        }

      }

      &--top {
        width: 100%;
        padding: 0 7px;
      }

      i {
        opacity: 0;
        visibility: hidden;
        position: absolute;
        top: 10px;
        right: 10px;
        width: 10px;
        height: 10px;
        border-radius: 50%;
        background-color: #cd084b;
        @include adaptive(767) {
          display: none;
        }
      }

      span,
      h5,
      p,
      h4 {
        @include transition(color);
      }

      span {
        opacity: 0;
        visibility: hidden;
        text-transform: uppercase;
        font-weight: bold;
        font-size: 10px;
        letter-spacing: 0.2em;
        line-height: 20px;
        text-align: left;
        color: #cd084b;
        @include adaptive(767) {
          display: none;
        }
      }

      h5,
      p {
        padding-bottom: 20px;
        font-weight: 600;
        font-size: 16px;
        line-height: 20px;
        color: #000;
        @include adaptive(767) {
          padding-bottom: 10px;
          font-size: 14px;
          line-height: 18px;
        }
      }

      h5 {
        display: none;
        @include adaptive(767) {
          display: block;
        }
      }

      p {
        @include adaptive(767) {
          display: none;
        }
      }

      h4 {
        font-weight: 600;
        font-size: 50px;
        line-height: 40px;
        color: #000;
        @include adaptive(1279) {
          font-size: 40px;
          line-height: 30px;
        }
        @include adaptive(1023) {
          font-size: 33px;
          line-height: 30px;
        }
        @include adaptive(767) {
          font-size: 26px;
          line-height: 20px;
        }
      }
    }
  }

  &__time {
    display: none;
    padding-top: 120px;
    padding-bottom: 140px;
    background-color: #f8fafc;
    @include adaptive(767) {
      padding-top: 60px;
      padding-bottom: 70px;
    }

    &-row {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;

      &-1 {
        padding-bottom: 60px;
        @include adaptive(767) {
          padding-bottom: 20px;

          .scheduler__time-col {
            max-width: 100%;
          }
        }
      }
    }

    &-col {
      width: 100%;
      max-width: calc((100% - 40px) / 2);
      @include adaptive(767) {
        max-width: calc((100% - 20px) / 2);
      }
    }

    &-btn {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 60px;
      margin-bottom: 20px;
      border-radius: 10px;
      background-color: #fff;
      box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.05);
      @include transition(box-shadow, background-color);
      @include adaptive(767) {
        margin-bottom: 10px;
      }

      &:last-of-type {
        margin-bottom: 0;
      }

      &.is-active,
      &:hover {
        box-shadow: none;
        background-color: #00449d;
        @include transition(box-shadow, background-color);

        span {
          color: #ffffff;
          @include transition(color);
        }
      }

      span {
        font-size: 20px;
        line-height: 30px;
        text-align: center;
        color: #000;
        @include transition(color);
        @include adaptive(767) {
          font-size: 16px;
          line-height: normal;
        }
      }
    }

    .c-form {
      &__label {
        color: rgba(#000, 0.5);
      }

      &__select {
        border: 2px solid rgba(0, 0, 0, 0.05);
      }
    }
  }

  &__information {
    display: none;

    &-copyright {
      font-size: 20px;
      line-height: 30px;
      color: #5390e3;
      @include adaptive(767) {
        font-size: 16px;
        line-height: normal;
      }
    }

    &.request {
      &:before {
        top: auto;
        right: -160px;
        bottom: -176px;
        margin-right: 0;
        background-size: contain;
        @include adaptive(767) {
          right: -75px;
          bottom: -75px;
          background-position: bottom right;
        }
      }

      .request {
        &__bg {
          width: 100%;
          margin-left: 0;
          border-radius: 0;
        }

        &__wrapper {
          padding-top: 120px;
          padding-bottom: 120px;
          @include adaptive(767) {
            padding-top: 60px;
            padding-bottom: 60px;
          }
        }
      }

      .c-form {
        &__row {
          padding-left: 0;
          padding-right: 0;
        }

        &__textarea {
          resize: none;
          border-radius: 10px;
          background-color: #fff;
          padding: 10px 20px;
          border: none;
          box-shadow: none;
          color: #000;
          font-size: 18px;

          &-wrapper {
            width: 100%;
            height: 200px;
          }
        }

        &__field {
          &--textarea {
            width: 100%;
          }

          &--btn {
            flex-direction: row;
            justify-content: flex-start;
            padding-bottom: 139px;
            @include adaptive(767) {
              padding-bottom: 100px;
            }

            .c-btn {
              max-width: 213px;
              margin-left: 0;
              margin-right: 20px;

              &:last-of-type {
                margin-right: 0;
              }

              &.c-btn--border {
                border: 2px solid #5390e3;
                color: #ffffff;
                @include transition(border, background-color, color);

                &:hover {
                  border-color: #CD084B;
                  @include transition(border, background-color, color);
                }
              }
            }
          }
        }
      }
    }
  }
}

// SECTION scheduler :: END